.user-info{padding:15px}.user-info .login-no span a{color:#000;margin-left:10px;line-height:24px;height:24px;vertical-align:top}.user-info .login-no span:before{content:'';display:inline-block;width:24px;height:24px;background:url("../assets/images/components/login_icon.png") no-repeat left top;background-size:24px 24px}.user-info .login-yes{background:url("../assets/images/components/go_icon.png") no-repeat right center;background-size:6px 10px}.user-info .login-yes div{display:inline-block;vertical-align:middle}.user-info .login-yes .avatar{width:40px;height:40px;border-radius:50%}.user-info .login-yes .avatar img{width:100%;height:100%;border-radius:50%}.user-info .login-yes .info{font-size:12px}



