.loading{position:fixed;top:0;left:0;background:transparent;z-index:9999;width:100%;height:100%}.loading .loading-inner{color:#fff;font-size:14px;position:absolute;top:50%;left:50%;background:rgba(0,0,0,0.5);-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);padding:50px 30px 10px 30px;border-radius:5px;text-align:center}.loading .loading-inner .loading-icon{display:block;width:24px;height:24px;position:absolute;top:15px;left:50%;margin-left:-14px;-webkit-transform:translateX(-50%);transform:translateX(-50%);border:2px solid #fff;border-radius:24px;-webkit-animation:myanimate 1s infinite linear;animation:myanimate 1s infinite linear;clip:rect(0 auto 12px 0)}@-webkit-keyframes myanimate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes myanimate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}



