.nv-alert{width:100%;height:100%;position:fixed;top:0;left:0;z-index:9999}.nv-alert .nv-alert_inner{position:absolute;background:rgba(0,0,0,0.5);color:#fff;font-size:14px;line-height:27px;top:30%;left:50%;text-align:center;border-radius:8px;padding:18px 30px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}



