.page-cover{background-color:rgba(0,0,0,0.4);position:fixed;z-index:5;top:0;left:0;width:100%;height:100%}#hd{-webkit-transition:all .3s ease;transition:all .3s ease;border-bottom:1px solid #e8e8e8}#hd .nv-toolbar{width:100%;height:44px;position:relative}#hd .nv-toolbar .toolbar-nav{width:49px;height:44px;background:url("../assets/images/components/nav_icon.png") no-repeat center center;background-size:19px 16px;margin:0;z-index:1;position:absolute;top:0;left:0}#hd .nv-toolbar span{width:100%;height:100%;display:block;font-size:16px;line-height:44px;text-align:center;position:absolute;z-index:0;top:0;left:0;font-size:16px}#hd .nv-toolbar .add-icon{color:#42b983;position:absolute;right:10px;top:10px;z-index:10;padding:5px 15px;border-radius:5px}.fix-header{width:100%;-webkit-transition:all .3s ease;transition:all .3s ease;position:fixed;top:0;left:0;background-color:#fff}.no-fix{width:100%;background-color:#fff;overflow:hidden}.show{-webkit-transform:translateX(200px);transform:translateX(200px)}



