.topic-con{margin-top:45px;padding-top:15px}.topic-con h3{font-size:18px;background-color:#f0f0f0;padding:5px;margin:15px;color:#2c3e50;margin-top:0}.topic-con .author-info{display:-webkit-box;display:-ms-flexbox;display:flex;padding:0px 15px;font-size:12px}.topic-con .author-info img{width:40px;height:40px;border-radius:50%;margin-right:15px}.topic-con .author-info .author-name{-webkit-box-flex:1;-ms-flex:1;flex:1}.topic-con .author-info .browser-num{-webkit-box-flex:1;-ms-flex:1;flex:1;text-align:right}.topic-con .author-info .browser-num p:first-child{display:block}.topic-con .author-info .browser-num p:first-child:before{font-size:10px;border-radius:4px;padding:0px 6px;color:#fff;line-height:24px;display:inline-block;height:24px}.topic-con .author-info .browser-num p:first-child.top:before{content:"置顶";background:#E74C3C}.topic-con .author-info .browser-num p:first-child.good:before{content:"精华";background:#E67E22}.topic-con .author-info .browser-num p:first-child.share:before{content:"分享";background:#1ABC9C}.topic-con .author-info .browser-num p:first-child.ask:before{content:"问答";background:#3498DB}.topic-con .author-info .browser-num p:first-child.job:before{content:"招聘";background:#9B59B6}.topic-con .topic-content{padding:15px;margin-top:15px;border-bottom:1px solid #d4d4d4}.topic-con .topic-content .markdown-text img{max-width:100%}.topic-con .topic-content .markdown-text p{width:100%;margin-bottom:15px}.topic-con .topic-content .markdown-text p img{max-width:100%}.topic-con .topic-content .markdown-text p a{color:#80bd01}.topic-con .reply_count{background-color:#fff}.topic-con .reply_count strong{color:#42b983}.topic-con .reply-list ul{width:100%;overflow:hidden}.topic-con .reply-list ul li{padding:15px 10px;border-top:1px solid #d4d4d4}.topic-con .reply-list ul li .reply-user{display:-webkit-box;display:-ms-flexbox;display:flex}.topic-con .reply-list ul li .reply-user img{width:40px;height:40px;margin-right:10px;border-radius:50%}.topic-con .reply-list ul li .reply-user .replier-name{-webkit-box-flex:1;-ms-flex:1;flex:1}.topic-con .reply-list ul li .reply-user .reply-distribute{-webkit-box-flex:1;-ms-flex:1;flex:1;text-align:right}.topic-con .reply-list ul li .reply-user .reply-distribute span{font-size:26px}.topic-con .reply-list ul li .reply-user .reply-distribute .uped{color:#80bd01}



