.login-body{padding:50px 15px;margin-top:45px}.login-body .label{width:100%;position:relative;margin-top:15px}.login-body .label input[type="text"]{border:0;padding:12px 0px;font-size:14px;width:100%;border-bottom:1px solid #4fc08d;outline:none}.login-body .label input[type="button"]{font-size:16px;border:0;background:#4fc08d;width:48%;outline:none;height:42px;color:#fff;border-radius:3px}.login-body .label input[type="file"]{opacity:0;position:absolute;top:0;left:0;height:42px;width:48%}



