#page{margin-top:45px}#page ul{list-style:none;margin:0;padding:0}#page ul li{border-bottom:1px solid #d5dbdb;padding:10px 15px}#page ul li h3{font-size:16px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}#page ul li h3:before{font-size:10px;padding:0px 6px;margin-right:10px;color:#fff;line-height:24px;display:inline-block;height:24px}#page ul li h3.top:before{content:"置顶";background:#E74C3C}#page ul li h3.good:before{content:"精华";background:#E67E22}#page ul li h3.share:before{content:"分享";background:#1ABC9C}#page ul li h3.ask:before{content:"问答";background:#3498DB}#page ul li h3.job:before{content:"招聘";background:#9B59B6}#page ul li div.content{display:-webkit-box;display:-ms-flexbox;display:flex;margin-top:10px}#page ul li div.content img{width:40px;height:40px;border-radius:50%;border:1px sold #f3f3f3;display:block;margin-right:10px}#page ul li div.content .info{-webkit-box-flex:1;-ms-flex:1;flex:1}#page ul li div.content .info p{margin:3px 0;line-height:1em;display:-webkit-box;display:-ms-flexbox;display:flex}#page ul li div.content .info p .name{-webkit-box-flex:1;-ms-flex:1;flex:1}#page ul li div.content .info p .status{color:#34495e}#page ul li div.content .info p .status b{color:#42b983}#page ul li div.content .info p time{font-size:12px;color:#34495e}#page ul li div.content .info p time:first-child{-webkit-box-flex:1;-ms-flex:1;flex:1}



